body {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  gap: 10px;
  margin: 20px auto;
  display: flex;
}

.search-form input {
  width: 300px;
  padding: 8px;
  font-size: 16px;
}

.search-form button {
  padding: 8px 16px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 16px;
  margin: 20px;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: opacity .3s ease-in-out;
  display: block;
}

.photo-card img[loading="lazy"] {
  opacity: 1;
}

.info {
  justify-content: space-around;
  padding: 10px;
  font-size: 14px;
  display: flex;
}

.info-item b {
  margin-bottom: 4px;
  display: block;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
}

.hidden {
  display: none;
}

